<template>
    <my-page :loading="isLoading">
        <template #heading>
            {{ $t("MY_INSTALLATION_HISTORY") }}
        </template>

        <div v-if="history.length" class="entries">
          <installation-history-entry
            v-for="entry of history"
            :key="entry.id"
            :entry="entry"
          />
        </div>
        <p v-else>
          {{ $t('INSTALLATION_HISTORY_EMPTY') }}
        </p>

        <template #sidebar>
            <p class="g-text">
                {{ $t('INSTALLATION_HISTORY_DESC') }}
            </p>
        </template>
    </my-page>
</template>

<script>
import MyPage from '../components/MyPage.vue';
import InstallationHistoryEntry from './components/InstallationHistoryEntry.vue';

export default {
  components: {
    MyPage,
    InstallationHistoryEntry,
  },
  data() {
    return {
      isLoading: true,
      history: [],
    };
  },
  computed: {
    title() {
      return this.$t('MY_INSTALLATION_HISTORY');
    },
  },
  methods: {
    async getHistory() {
      this.isLoading = true;

      try {
        this.history = await this.$api.getInstallationHistory();
      } finally {
        this.isLoading = false;
      }
    },
  },
  created() {
    this.getHistory();
  },
};
</script>

<style lang="scss" scoped>
@import "@/assets/variables";

.entries {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
</style>
