<template>
    <div class="entry">
      <div class="info">
        <div class="info__top">
          <app-icon class="entry__icon" :src="entry.object.icon_uri" size="57px" />
          <div class="details">
            <p class="heading">
              {{ entry.object.name }}<template v-if="entry.object.version">, v{{ entry.object.version }}</template>
            </p>

            <p class="description" v-if="entry.alongside_id">
              {{ $t('INSTALLED_TO_DEVICE_ALONGSIDE', {
                installedAt: new Date(entry.queued_at * 1000).toLocaleString(),
                alongsideID: entry.alongside_id,
                deviceModel: entry.device_model,
                deviceName: entry.device_name }) }}
            </p>
            <p class="description" v-else>
              {{ $t('INSTALLED_TO_DEVICE', {
                installedAt: new Date(entry.queued_at * 1000).toLocaleString(),
                deviceModel: entry.device_model,
                deviceName: entry.device_name }) }}
            </p>
          </div>
        </div>
        <div v-if="entry.error" class="error">
          {{ entry.error?.translated }}
        </div>
      </div>

      <div class="actions">
        <v-button
          ref="installButton"
          type="primary"
          :loading="isInstalling"
          tooltip="Installation was queued"
          @click.native="install"
        >
          {{ $t('REINSTALL') }}
        </v-button>
        <v-button
          v-if="downloadLink"
          tag="router-link"
          :to="downloadLink"
          target="_blank"
        >
          {{ $t('DOWNLOAD') }}
        </v-button>
        <v-button
          tag="router-link"
          :to="`/details/${entry.object.universal_object_identifier}`"
        >
          {{ $t('VIEW_IN_STORE') }}
        </v-button>
      </div>
    </div>
</template>

<script>
import AppIcon from '@/components/AppIcon.vue';
import VButton from '@/components/Button.vue';
import installApp from '@/mixins/installApp';

export default {
  mixins: [installApp],
  components: {
    AppIcon,
    VButton,
  },
  props: {
    entry: Object,
  },
  data() {
    return {
      isInstalling: false,
    };
  },
  computed: {
    downloadLink() {
      const ticket = this.entry.download_ticket;
      if (!ticket) {
        return '';
      }

      return `/redirect/${ticket}`;
    },
  },
  methods: {
    async install() {
      if (this.entry.no_installation_ticket_failure_reason) {
        this.$notify({
          type: 'error',
          text: this.entry.no_installation_ticket_failure_reason.translated,
        });
      }

      this.isInstalling = true;
      try {
        await this.installApp(this.entry.object.id, 'universal', { installationTicket: this.entry.installation_ticket, showSuccessConfirm: false });
        this.$refs.installButton.showTooltip({ content: this.$t('INSTALLATION_QUEUED') });
      } finally {
        this.isInstalling = false;
      }
    },
  },
};
</script>

