  display: flex;
  gap: 15px;
  padding: 10px;
  border: 1px solid var(--tertiary-accent);
  border-radius: 5px;
}

.heading {
  font-size: 16px;
}

.description {
  line-height: 1.2;
  margin-top: 6px;
}

.info {
  &__top {
    display: flex;
    gap: 15px;
  }
}

.error {
  margin-top: 10px;
  color: var(--text-error);
}

.details,
.actions {
  display: flex;
  flex-direction: column;
}

.actions {
  margin-left: auto;
  flex-shrink: 0;
  gap: 3px;
}
</style>
